import AddCircleOutlineSharpIcon from '@mui/icons-material/AddCircleOutlineSharp';
import Select from 'react-select';
import { createDesiredGraph } from './RequestUtils';
import { useState } from 'react';

export default function CreateGraph({ botId, updateStateFunction }) {

    const [selectedTag, setSelectedTag] = useState('DAILY');
    const [selectedType, setSelectedType] = useState('Gastos en los ltimos 7 das');

    const tagoptions = [
        {value: 'DAILY', label: 'Diariamente'},
        {value: 'WEEKLY', label: 'Semanalmente'},
        {value: 'MONTHLY', label: 'Mensualmente'},
    ]

    const typeoptions = [
        {value: 'Gastos en los ltimos 7 das', label: 'Gastos en los ltimos 7 das'},
        {value: 'Gastos en los ltimos 7 das por categora', label: 'Gastos en los ltimos 7 das por categora'},
        {value: 'Gastos en los ltimos 30 das', label: 'Gastos en los ltimos 30 das'},
        {value: 'Gastos en los ltimos 30 das por categora', label: 'Gastos en los ltimos 30 das por categora'},
        {value: 'Ingresos desde noviembre por categora', label: 'Ingresos desde noviembre por categora'},
        {value: 'Ahorros desde noviembre', label: 'Ahorros desde noviembre'},
        {value: 'Ahorros desde noviembre acumulado', label: 'Ahorros desde noviembre acumulado'},
    ]

    return (
        <div className='usergraph__item usergraph__createcontainer'>
            <button className='usergraph__createbutton' onClick={async function() {
                const apiResponse = await createDesiredGraph(
                    JSON.stringify(
                        {
                            userId: "", // Will be filled up by backend
                            botId: botId,
                            tag: selectedTag,
                            type: selectedType
                        }
                    )
                )
                if(apiResponse) {
                    updateStateFunction()
                }
            }}>
                <p>Crear nueva grfica</p>
                <AddCircleOutlineSharpIcon />
            </button>
            <div className='usergraph__createtagrow'>
                <Select
                    defaultValue={typeoptions[0]}
                    options={typeoptions}
                    onChange={function (newType) {
                        setSelectedType(newType.value)
                    }}
                />
                <Select
                    defaultValue={tagoptions[0]}
                    options={tagoptions}
                    onChange={function (newTag) {
                        setSelectedTag(newTag.value)
                    }}
                />
            </div>
        </div>
    )
}